export const getRoleData = ({
  values,
  role
}) => {
  const {
    name,
    description,
    metadataAuthorities,
    appAuthorities,
    trackerAuthorities,
    importExportAuthorities,
    systemAuthorities
  } = values;
  return {
    id: role === null || role === void 0 ? void 0 : role.id,
    name,
    description,
    authorities: [...metadataAuthorities, ...appAuthorities, ...trackerAuthorities, ...importExportAuthorities, ...systemAuthorities]
  };
};